import React,{Component} from 'react';
import DetailType1 from './detailTypes/detailType1';
import Intro from './detailTypes/intro';
import News from './detailTypes/news';
import History from './detailTypes/history';
import Recruit from './detailTypes/recruit';
import '../css/details.css';

class Details extends Component{
	constructor(){
		super()
	}
	componentDidMount(){
		/*
		 * 根据浏览器窗口的大小和paging 分页的显示状态来改变paging的分页
		 */
		(this.refs.paging.offsetWidth<=700)&&(this.refs.paging.style.right='-700px');
		window.addEventListener('resize',()=>{
			if(this.refs.paging.style.right!='0%'){
				if(this.refs.paging.offsetWidth<=700){
					this.refs.paging.style.right='-700px'
				}else{
					this.refs.paging.style.right='-80%';
				}
			}
		},false)
	}
	render(){
		return (
			<div className='paging' ref='paging' style={{right:'-80%'}}>
				{(function(){
					/*
					 * 判断进入页面的类型
					 * 返回相对应的 组件
					 */
					switch(this.props.linkType){
						case 1:return <DetailType1 data={this.props.detailsType1_data} ref='paging_child' num={this.props.num}/>;break;
						case 2:return <Intro ref='paging_child' data={this.props.intro_data}/>;break;
						case 3:return <News ref='paging_child' data={this.props.news_data} />;break;
						case 4:return <History ref='paging_child' data={this.props.history_data}/>;break;
						case 5:return <Recruit ref='paging_child' data={this.props.recruit_data}/>;break;
					}
				}.call(this))}
			
				{/*关闭分页按钮*/}
			    <div className='closePaging' ref='closePaging' onClick={this.props.closePaging}>
			    	<span>{this.props.val}</span>
			    	<div className='closePaging_x'>
			    		<span></span>
			    		<span></span>
			    	</div>
			    </div>
			</div>
		)
	}
}
export default Details;



