import React,{Component} from 'react';
import '../css/ECproduct.css';
import img from '../img/EC1.jpg';

class ECproduct extends Component{
	constructor(){
		super()
		this.state={
			cls:['cld1','cld2','cld3','cld4','cld5'],
			data:[]
		}
	}
	/*
	  * 	获取数据
	  */
	componentDidMount(){
		fetch('http://localhost:8004/ECproduct/get')
		.then(res=>res.json())
		.then(res=>(
			this.setState({
				data:res
			})
		))
	}
	/*
	 * 上一个按钮
	 */
	prev(){
		this.state.cls.unshift(this.state.cls.pop())
		this.setState({})
	}
	/*
	 * 下一个按钮
	 */
	next(){
		this.state.cls.push(this.state.cls.shift())
		this.setState({})
	}
	/*
	 * 点击图片切换
	 */
	tabCls(clsName){
		switch(clsName){
			case 'cld4':this.prev();break;
			case 'cld2':this.next();break;
			case 'cld5':this.prev();this.prev();break;
			case 'cld1':this.next();this.next();break;
		}
	}
	render(){
		return (
			<div className='ECproduct'>
				{/*
			  	* 头部*/}
				<div className='cC_head'>
					<h2>ELECTRICITY SERVICES</h2>
					<div className='cC_line'></div>
					<p className='cC_txt'>开夏辟唐电商服务</p>
				</div>
				
				{/*
				  * 	轮播内容*/}
				<div className='EC_con'>
					<span className='prev' ref='prev' onClick={this.prev.bind(this)}></span>
					<span className='next' ref='next' onClick={this.next.bind(this)}></span>
					<ul ref='oli'>
						{/*
						  * 	遍历数据创建元素
						  */}
						{
							this.state.data.length&&
							this.state.data.map((v,k)=>(
								<li key={k} className={this.state.cls[k]} onClick={this.tabCls.bind(this,this.state.cls[k])}>
									<a><img src={v.img}/></a>
									<div className='EC_txt'>
										<h3>{v.tit}</h3>
										<p>{v.txt}</p>
									</div>
								</li>
							))
						}
					</ul>
				</div>
			</div>
		)
	}
}
export default ECproduct;


