import React,{Component} from 'react';
import '../css/dataProduct.css';

class DataProduct extends Component {
	constructor(){
		super()
		this.state={
			data:[]
		}
	}
	componentDidMount(){
		fetch('http://localhost:8004/dataProduct/get')
		.then(res=>res.json())
		.then(data=>{
			this.setState({
				data:data
			})
		})
	}
	render(){
		return (
		/*容器*/
			<div className='dataProduct'>
			{/*
			  * 头部*/}
				<div className='cC_head'>
					<h2>DATA PRODUCTS</h2>
					<div className='cC_line'></div>
					<p className='cC_txt'>智唐科技数据产品</p>
				</div>
				{/*
				  * 	内容主体*/}
				<div className='data_pro_list'>
					<ul>
						{/*
						  * 	循环获取到的数据创建标签
						  */}
						{
							this.state.data.length&&
							this.state.data.map((v,k)=>(
								<li key={k}>
									<div className='data_pro_child'>
										<div className='dpc_rotate'>
											<div className='dpc_rotate_child1'></div>
											<div className='dpc_rotate_child2'></div>
											<img src={v.img}/>
										</div>
										<p className='dpc_title'>{v.title}</p>
										<div className='dpc_line'></div>
										<p className='dpc_txt'>{v.txt}</p>
									</div>
								</li>
							))
						}
					</ul>
				</div>
			</div>
		)
	}
}
export default DataProduct;


