import React,{Component} from 'react';
import '../../css/detailTypes/recruit.css';

class Recruit extends Component {
	constructor(){
		super();
		this.state={
			start:0,
			end:10
		}
	}
	/*
	 * 返回上一级  列表页
	 */
	handleBack(){
		this.refs.Recruit_detail.style.opacity='0';
		this.refs.Recruit_items.style.display='block';
		setTimeout(()=>{
			this.refs.Recruit_detail.style.display='none';
		},300)
	}
	
	/*
	 * 进入详情页
	 */
	itemShow(num,type){
		/*
		 * num:点击的那一条
		 * type:Number类型   0代表获取当前id的详情 1下一个   -1  上一个
		 */
		this.refs.Recruit_detail.style.display='block';
		setTimeout(()=>{
			this.refs.Recruit_detail.style.opacity='1';
			this.refs.Recruit_items.style.display='none';
		},300)
		this.num=num;
		this.getData(num,type)
	}
	/*
	 * 调取数据
	 */
	getData(num,type){
		switch(type){
			case 0:var Url='http://localhost:8004/recruit/getDetail';break;
			case -1:var Url='http://localhost:8004/recruit/getDetailPrev';break;
			case 1:var Url='http://localhost:8004/recruit/getDetailNext';break;
		}
		fetch(Url,{
			method:'post',
			headers:{
		    	"Content-type":"application/x-www-form-urlencoded; charset=UTF-8"
		    },
			body:`id=${num}`
		}).then(res=>{
			if(res.ok){
				return res.json()
			}
		}).then(res=>{
			if(!res.length) return;
			if(type== 1 || type == -1) this.num=res[0].id;
			this.refs.detail_box.innerHTML=res[0].content;
			this.refs.title.innerHTML=res[0].title;
			this.refs.detail_mail.innerHTML=res[0].mail;
			
		}).catch(e=>{
			console.log(e)
		})
	}
	/*
	 * 上一页
	 */
	prev(){
		this.getData(this.num,-1)
	}
	/*
	 * 下一页
	 */
	next(){
		this.getData(this.num,1)
	}
	render(){
		return (
			<div className='Recruit'>
				<div className='Recruit_items' ref='Recruit_items'>
					<div className='controller'>
						{/*
						  * 	上一个
						  */}
						<a href='javascript:;' className='ctl_prev'>
							<span></span>
							<span></span>
						</a>
						{/*
						  * 	下一个
						  */}
						<a href='javascript:;' className='ctl_next'>
							<span></span>
							<span></span>
						</a>
					</div>
					{/*
					  * 	列表项
					  */}
					<ul className='Recruit_list'>
						{
							this.props.data.map((v,k)=>(
								<li key={k} onClick={this.itemShow.bind(this,v.id,0)}>
									<h3>{v.name}</h3>
									<p>{v.type}</p>
									<div className='line'></div>
									<p>{v.city}</p>
									<p>招聘人数：{v.num}</p>
								</li>
							))
						}
					</ul>
				</div>
				<div className='Recruit_detail' ref='Recruit_detail'>
					<div className='controller'>
						{/*
						  * 	回到上一级
						  */}
						<a href='javascript:;' className='ctl_back' onClick={this.handleBack.bind(this)}>{(function(){
								var arr=[];
								for(var i=0;i<9;i++){
									arr.push(<span key={i}></span>)
								}
								return arr;
							}())}
						</a>
						{/*
						  * 	上一个
						  */}
						<a href='javascript:;' className='ctl_prev' onClick={this.prev.bind(this)}>
							<span></span>
							<span></span>
						</a>
						{/*
						  * 	下一个
						  */}
						<a href='javascript:;' className='ctl_next' onClick={this.next.bind(this)}>
							<span></span>
							<span></span>
						</a>
					</div>
					{/*
					  * 列表详情页	
					  * */}
					<div ref='Recruit_detail_con' className='Recruit_detail_con'>
						<div className='Recruit_title' ref='title'></div>
						<div className='detail_box' ref='detail_box'></div>
						<div className='detail_btm'>
							请发送邮件至：<span className='detail_mail' ref='detail_mail'></span> 提交简历
						</div>
					</div>
				</div>
			</div>
		)
	}
}
export default Recruit;


