import React,{Component} from 'react';
import '../css/sideNav.css';
import logo from '../img/logo.jpg';
import weiboQR from '../img/weiboQR.png';
import zhitangQR from '../img/zhitangQR.jpg';
import qizhiQR from '../img/qizhiQR.jpg';

class SideNav extends Component{
	constructor(){
		super()
	}
	/*右侧导航栏的切出/切回*/
	sideNavToggle(){
		this.refs.sideNav_btn.classList.toggle('sideNav_btn_In');
		this.refs.sideNav.classList.toggle('sideNavIn');
		this.refs.sideNav.parentNode.children[1].classList.toggle('conIn')
	}
	render(){
		return (
			<div className='sideNav sideNavIn' ref='sideNav'>
				{/*
				  * 	切换导航按钮
				  * */}
				<div className='sideNav_btn' ref='sideNav_btn' onClick={this.sideNavToggle.bind(this)}>
					<div></div>
					<div></div>
					<div></div>
				</div>
				{/*
				  * 	导航主体内容
				  * */}
				<div className='sideNav_con'>
				{/*
				  * 	logo
				  * */}
					<img src={logo} alt='智唐' className='logo'/>
					{/*
					  * 	导航
					  * */}
					<ul className='s_c_list' ref='s_c_list'>
						<li className='active' onClick={()=>{this.props.handleClick(0)}}>
							<a href='javascript:;' className='ot'>首页</a>
							<a href='javascript:;' className='in'>首页</a>
						</li>
						<li onClick={()=>{this.props.handleClick(1)}}>
							<a href='javascript:;' className='ot'>子公司</a>
							<a href='javascript:;' className='in'>子公司</a>
						</li>
						<li onClick={()=>{this.props.handleClick(2)}}>
							<a href='javascript:;' className='ot'>数据产品</a>
							<a href='javascript:;' className='in'>数据产品</a>
						</li>
						<li onClick={()=>{this.props.handleClick(3)}}>
							<a href='javascript:;' className='ot'>营销服务</a>
							<a href='javascript:;' className='in'>营销服务</a>
						</li>
						<li onClick={()=>{this.props.handleClick(4)}}>
							<a href='javascript:;' className='ot'>电商产品</a>
							<a href='javascript:;' className='in'>电商产品</a>
						</li>
						<li onClick={()=>{this.props.handleClick(5)}}>
							<a href='javascript:;' className='ot'>关于我们</a>
							<a href='javascript:;' className='in'>关于我们</a>
						</li>
						<li onClick={()=>{this.props.handleClick(6)}}>
							<a href='javascript:;' className='ot'>联系我们</a>
							<a href='javascript:;' className='in'>联系我们</a>
						</li>
					</ul>
					{/*
					  * 	导航底部练习方式
					  * */}
					<div className='s_c_btm'>
						<p>8610-65513781</p>
						<ul>
							<li><span className='weiboQR'></span><div className='weiboQRBig'><img src={weiboQR}/></div></li>
							<li><span className='zhitangQR'></span><div className='zhitangBig'><img src={zhitangQR}/></div></li>
							<li><span className='qizhiQR'></span><div className='qizhiQRBig'><img src={qizhiQR}/></div></li>
						</ul>
					</div>
				</div>
			</div>
		)
	}
}
export default SideNav;


