import React,{Component} from 'react';
import '../css/childCompany.css';

class ChildCompany extends Component {
	constructor(){
		super()
		/*
		 * 初始化变量
		 */
		this.state={
			data:[]
		}
		this.num=0;
	}
	
	componentDidMount(){
		/*
		 * 获取数据
		 */
		fetch('http://localhost:8004/childcompany/get')
		.then(res=>res.json())
		.then(data=>{
			this.setState({
				data:data
			})
		})
	}
	/*左按钮*/
	handleleft(){
		this.refs.right.classList.remove('cC_btn_out');
		this.num--;
		if(this.num<=0){
			this.num=0;
			this.refs.left.classList.add('cC_btn_out');
		} 
		this.refs.cC_con_long.style.marginLeft=-this.num*270+'px';
	}
	/*
	 * 右按钮
	 */
	handleright(){
		this.refs.left.classList.remove('cC_btn_out');
		this.num++;
		if(this.num>=2){
			this.num=2;
			this.refs.right.classList.add('cC_btn_out');
		} 
		this.refs.cC_con_long.style.marginLeft=-this.num*270+'px';
	}
	render(){
		return (
		/*容器*/
			<div className='childCompany'>
			{/*
			  * 头部*/}
				<div className='cC_head'>
					<h2>ZHITANG GROUP</h2>
					<div className='cC_line'></div>
					<p className='cC_txt'>智唐集团旗下公司</p>
				</div>
				{/*
				  * 	轮播
				  */}
				<div className='cC_slider'>
					{/*
					  * 	左按钮
					  */}
					<span ref='left' className='cC_btn cC_btn_left cC_btn_out' onClick={this.handleleft.bind(this)}>&lt;</span>
					<div className='cC_con'>
						{/*
						  * 	宽盒子
						  */}
						<div className='cC_con_long' ref='cC_con_long'>
							{/*
							  * 	遍历数据获取添加元素
							  */}
							{
							this.state.data.length&&
							this.state.data.map((v,k)=>(
							<div key={k}>
								<div className='cC_con_main'>
									<img src={v.img}/>
									<div className='cC_con_hover_main'>
										<img src={v.img}/>
										<p>{v.txt}</p>
									</div>
								</div>
								<a href='javascript:;' className='cC_con_detail'>Detaid →</a>
							</div>))
							}
						</div>
					</div>
					{/*
					  * 	右按钮
					  */}
					<span ref='right' className='cC_btn cC_btn_right' onClick={this.handleright.bind(this)}>&gt;</span>
				</div>
			</div>
		)
	}
}
export default ChildCompany;


