import React,{Component} from 'react';
import '../css/marketing.css';

class Marketing extends Component{
	constructor(){
		super();
		this.state={
			data:[]
		}
	}
	componentDidMount(){
		fetch('http://localhost:8004/marketing/get')
		.then(res=>res.json())
		.then(res=>{
			this.setState({
				data:res
			})
		})
	}
	/*
	 * 下侧横拉条
	 */
	scrollDown(e){
		e.preventDefault();
		var offX=e.clientX;
		var x=this.refs.scrollIn.offsetLeft;
		document.onmousemove= e => {
			var ox=e.clientX-offX;
			var xx=x+ox;
			xx<0?xx=0:xx>(1326-1247)?xx=(1326-1247):null;
			this.refs.marketing_con.style.marginLeft=-xx/(1326-1247)*(1452-1366)+'px';
			this.refs.scrollIn.style.left=xx+'px';
		}
		document.onmouseup= () => document.onmousemove=document.onmouseup=null;
	}
	render(){
		return(
			<div className='marketing'>
			{/*
			  * 	主要内容*/}
				<ul className='marketing_con' ref='marketing_con'>
				{/*
				  * 	获取数据遍历出dom节点
				  * */}
				{
					this.state.data.length&&
					this.state.data.map((v,k)=>(
						<li key={k} onClick={this.props.click.bind(this,v.tit,k)}>
							<img src={v.img}/>
							<div className='mc_txtWrap'>
								<p className='mc_txtWrap_tit'>{v.eng}</p>
								<div className='mc_txtWrap_con'>
									<p>{v.tit}</p>
									<p>{v.txt1}</p>
									<p>{v.txt2}</p>
								</div>
							</div>
						</li>
					))
				}
				</ul>
				{/*
				  * 底部横拉条	
				  * */}
				<div className='scrollMove'>
					<a href='javascript:;' className='scrollMove_left'></a>
					<div className='scrollMove_child'>
						<div></div>
						<div ref='scrollIn' onMouseDown={this.scrollDown.bind(this)}></div>
					</div>
					<a href='javascript:;' className='scrollMove_right'></a>
				</div>
			</div>
		)
	}
}
export default Marketing;



