import React,{Component} from 'react';
import '../../css/detailTypes/history.css';

class History extends Component{
	constructor(){
		super();
		this.state={
			dataArr:[]
		}
	}
	componentWillReceiveProps(e){
		/*
		 * 提取数据中的年份
		 */
		for(var i of e.data.reverse()){
			if(this.state.dataArr.indexOf(i.year)==-1){
				this.state.dataArr.push(i.year)
			}
		}
	}
	render(){
		return (
			<div className='hty'>
			{
				this.state.dataArr.map((v,k)=>(
					/*
					  * 	年分列表
					  */
					<div className='hty_list' key={k}>
						<div className='year'>{v}</div>
							<div className='year_list'>
							{this.props.data.reverse().filter(e=>(e.year==v)).map((val,Key)=>(
								/*
								  * 	月份列表
								  */
								<div className='year_item' key={Key}>
									<div className='month'>{val.month}</div>
									<div className='title'>{val.title}</div>
									<div className='Y_txt' dangerouslySetInnerHTML={{
					                    __html: val.txt
					                }}></div>
								</div>
							))}
						</div>
					</div>
				))
			}
			</div>
		)
	}
}
export default History;


