import React,{Component} from 'react';
import img from './img/enter.gif';

let sty={
	width:'100%',
	height:'100%',
	background:'#2A3D4F',
	position:'absolute',
	zIndex:'12'
}
let imgSty={
	position:'absolute',
	left:'50%',
	top:'50%',
	transform:'translateX(-50%) translateY(-50%)'
}
class Enter extends Component{
	constructor(){
		super()
	}
	componentWillMount(){
		window.onload=function(){
			this.refs.enter.style.display='none';
		}.bind(this)
	}
	render(){
		return (
			<div style={sty} ref='enter'><img src={img} style={imgSty}/></div>
		)
	}
}
export default Enter;


