import React,{Component} from 'react';
import '../css/contact_us.css';

class Contact extends Component{
	constructor(){
		super()
		this.old=0;
		this.state={
			data:[]
		}
	}
	componentDidMount(){
		fetch('http://localhost:8004/contact/get')
		.then(res=>res.json())
		.then(res=>{
			this.setState({
				data:res
			})
		})
	}
	tabCity(num){
		this.refs.mapIn.children[this.old].classList.remove('in');
		this.refs.mapIn.children[num].classList.add('in');
		this.refs.txt.children[this.old].classList.remove('in');
		this.refs.txt.children[num].classList.add('in');
		this.old=num;
	}
	render(){
		return (
			<div className='map'>
				<div className='mapIn' ref='mapIn'>
					{/*
					  * 	遍历数据穿件元素
					  */}
					{
						this.state.data.length&&
						this.state.data.map((v,k)=>(
							<span key={k} className={!k?'in':''} onClick={this.tabCity.bind(this,k)}>{!k?<b>{v.city}</b>:v.city}</span>
						))
					}
				</div>
				<div className='txt' ref='txt'>
					{/*
					  * 	遍历数据穿件元素
					  */}
					{
						this.state.data.length&&
						this.state.data.map((v,k)=>(
							<div key={k} className={!k?'in':''}>
								<p>{v.tit}</p>
								<p>{v.txt}</p>
								<p>{v.phone}</p>
							</div>
						))
					}
				</div>
			</div>
		)
	}
}
export default Contact;


