import React,{Component} from 'react';
import '../css/about.css';
import img from '../img/about1.png';


class About extends Component{
	constructor(){
		super();
		this.state={
			data:[]
		}
	}
	componentDidMount(){
		fetch('http://localhost:8004/about/get')
		.then(res=>res.json())
		.then(res=>{
			this.setState({
				data:res
			})
		})
	}
	render(){
		return (
			<div className='about'>
				{/*
				  * 	主体内容
				  */}
				<div className='about_con'>
					{/*
					  * 	标题
					  */}
					<div className='a_c_tit'><p>ABOUT US</p><p>关于我们</p></div>
					{/*
					  * 	列表项
					  */}
					<div className='a_c_list'>
						{/*
						  * 	遍历数据添加元素
						  */}
						{
							this.state.data.length&&
							this.state.data.map((v,k)=>(
								<div key={k} className='list_cld' onClick={this.props.click.bind(this,k+2,v.tit,0)}>
									<div className='list_cld_dft'>
										<img src={v.img}/>
										<p>{v.eng}</p>
										<p>{v.tit}</p>
									</div>
									<div className='list_cld_hover'>
										<div className='hover_lines'>
											<div></div>
											<div></div>
											<div></div>
											<div></div>
										</div>
										<div className='hover_con'>
											<div className='bias'></div>
											<span className='hover_txt'>VIEW</span>
											<span className='hover_txt'>查看</span>
										</div>
									</div>
								</div>
							))
						}
					</div>
				</div>
			</div>
		)
	}
}
export default About;


