import React, {Component} from 'react';
import './css/App.css';
import SideNav from './components/sideNav';
import Footer from './components/footer';
import Home from './components/home';
import ChildCompany from './components/childCompany';
import DataProduct from './components/dataProduct';
import Marketing from './components/marketing';
import Details from './components/details';
import ECproduct from './components/ECproduct';
import About from './components/about';
import Contact from './components/contact_us';
import close from './img/close.png';


let Boo=true,num=0,child;
class App extends Component {
	constructor() {
		super();
		this.state={
			val:'',
			/*  linkType:
			 * 进入详情页的类型（Number）
			 * 1 从营销服务页进入
			 * 2 从关于我们  企业详情处进入
			 * 3 从关于我们  公司新闻处进入
			 * 4 从关于我们  成长历程处进入
			 * 5 从关于我们  企业招聘处进入
			 * */
			linkType:null,
			detailsType1_data:[],//marketing详情的数据初始化
			intro_data:[],//公司简介的分页数据初始化
			news_data:[],//公司新闻的分页数据初始化
			history_data:[],//成长历史的分页数据初始化
			recruit_data:[],//企业招聘的分页数据初始化
			num:null
		}
		/*
		 * 详情页的打开状态
		 */
		this.paging=false;
	}
	/*
	 * 鼠标滚轮事件封装
	 */
	Mousewheel(obj,fn1){
		var boo;
		obj.onmousewheel=fn;
		obj.addEventListener('DOMMouseScroll',fn,false);
		if(arguments.length>2){
			var a=arguments[2];
		}
		function fn(e){
			if(a){
				e.preventDefault();
				e.returnValue=false;
			}
			if(e.wheelDelta!=undefined){
				if(e.wheelDelta<0){
					boo=true;
				}else{
					boo=false;
				}
			}else{
				if(e.detail>0){
					boo=true;
				}else{
					boo=false;
				}
			}
			fn1(boo)
		}
	}
	/*
	 * 样式切换函数
	 */
	TabStyle(state){
		this.child[num].style.transformOrigin='center bottom';
		this.child[num].classList.add('DownRoll');
		this.Navs[num].classList.remove('active');
		state?num+=1:num-=1;
		num==3?this.refs.footer.refs.foo.classList.add('footer_hide'):this.refs.footer.refs.foo.classList.remove('footer_hide');
		this.child[num].style.transformOrigin='center top';
		this.child[num].classList.add('UpRoll');
		this.Navs[num].classList.add('active');
		/*
		 * 动画完毕后复位
		 */
		setTimeout(()=>{
			this.child[num].classList.remove('UpRoll');
			this.child[num].classList.add('default');
			this.child[num+(state?(-1):1)].classList.remove('DownRoll', 'default','UpRoll');
			this.child[num+(state?(-1):1)].style='';
			Boo=true;
		},500)
	}
	componentDidMount(){
		/*
		 * dom 渲染完毕后绑定滚轮事件
		 */
		this.Navs=this.refs.sideNav.refs.s_c_list.children;
		this.child=this.refs.con.children;
		this.Mousewheel(document,(state)=>{
			if(this.paging)return;
			if(state){
				if(num==this.child.length-1)return;
			}else{
				if(num==0)return;
			}
			if(Boo){
				Boo=false;
				state?this.TabStyle(state):this.TabStyle(state);
			}
		})
	}
	/*
	 * 点击导航切换页面的函数
	*/
	tabConClick(e){
		if(num==e)return;
		this.child[num].style.transformOrigin='center bottom';
		this.child[num].classList.add('DownRoll');
		this.Navs[num].classList.remove('active');
		e==3?this.refs.footer.refs.foo.classList.add('footer_hide'):this.refs.footer.refs.foo.classList.remove('footer_hide');
		this.child[e].style.transformOrigin='center top';
		this.child[e].classList.add('UpRoll');
		this.Navs[e].classList.add('active');
		setTimeout(()=>{
			this.child[e].classList.remove('UpRoll');
			this.child[e].classList.add('default');
			this.child[num].classList.remove('DownRoll', 'default','UpRoll');
			this.child[num].style='';
			num=e;
			Boo=true;
		},500)
	}
	/*
	 * 欢迎图
	 */
	handleClose(){
		this.refs.close.style.transition='.5s';
		this.refs.close.style.opacity='0';
		setTimeout(()=>{
			this.refs.close.style.display='none';
		},500)
			
	}
	/*
	 * 查看页面信息详情出现内容
	 * 打开分页
	 */
	handleShow(linkType,val,k){
		/*
		 * val 点击的入口标题
		 * k  点的哪一个
		 * 每个入口对应的后台接口
		 */
		let urls=[
			['http://localhost:8004/marketing_child1/get',
			'http://localhost:8004/marketing_child2/get',
			'http://localhost:8004/marketing_child3/get',
			'http://localhost:8004/marketing_child4/get',],
			['http://localhost:8004/intro/get'],
			['http://localhost:8004/news/get'],
			['http://localhost:8004/history/get'],
			['http://localhost:8004/recruit/get']
		]
		fetch(urls[linkType-1][k])
		.then(res=>res.json())
		.then(data=>{
			/*
			 * 给对应的类型页存储获取到的数据
			 */
			switch(linkType){
				case 1:this.setState({
					detailsType1_data:data
				});break;
				case 2:this.setState({
					intro_data:data
				});break;
				case 3:this.setState({
					news_data:data
				});break;
				case 4:this.setState({
					history_data:data
				});break;
				case 5:this.setState({
					recruit_data:data
				});break;
			}
		}).catch(e=>{
			switch(linkType){
				case 1:this.setState({
					detailsType1_data:[]
				});break;
				case 2:this.setState({
					intro_data:[]
				});break;
				case 3:this.setState({
					news_data:[]
				});break;
				case 4:this.setState({
					history_data:[]
				});break;
				case 5:this.setState({
					recruit_data:[]
				});break;
			}
		})
		/*
		 * 让蒙版出来
		 */
		this.refs.mask.style.display='block';
		/*
		 * 让分页出来
		 */
		this.refs.closePaging.refs.closePaging.style.right='78%';
		this.refs.closePaging.refs.closePaging.parentNode.style.right='0%';
		/*
		 * 更新ui传递点击的当前标题
		 * 传递点击的类型
		 * 传递当前点击的索引
		 */
		this.setState({
			val:val,
			linkType:linkType,
			num:k
		})
		/*
		 * 滚轮切换开关  
		 * 打开分页后关闭开关 true==关闭功能
		 */
		this.paging=true;
	}
	/*
	 * 关闭分页
	 */
	closePaging(){
		setTimeout(()=>{
			this.refs.mask.style.display='none';
			if(this.refs.closePaging.refs.paging_child.refs.DT1_itemShow){
				this.refs.closePaging.refs.paging_child.refs.DT1_itemShow.style.opacity='0';
				this.refs.closePaging.refs.paging_child.refs.DT1_itemShow.style.display='none';
			}
			if(this.refs.closePaging.refs.paging_child.refs.news_detail){
				this.refs.closePaging.refs.paging_child.refs.news_detail.style.opacity='0';
				this.refs.closePaging.refs.paging_child.refs.news_detail.style.display='none';
				this.refs.closePaging.refs.paging_child.refs.news_items.style.display='block';
			}
			if(this.refs.closePaging.refs.paging_child.refs.Recruit_detail){
				this.refs.closePaging.refs.paging_child.refs.Recruit_detail.style.opacity='0';
				this.refs.closePaging.refs.paging_child.refs.Recruit_detail.style.display='none';
				this.refs.closePaging.refs.paging_child.refs.Recruit_items.style.display='block';
			}
		},600)
		this.paging=false;
		this.refs.closePaging.refs.closePaging.style.right='';
		this.refs.closePaging.refs.closePaging.parentNode.style.right='-80%';
		(this.refs.closePaging.refs.paging.offsetWidth<=700)&&(this.refs.closePaging.refs.paging.style.right='-700px');
	}
	render() {
		return(
			<div className='Wrap'>
				{/*
				  * 	广告欢迎页
				*/}
				<div className='banner' ref='close' >
					<div className='close' onClick={this.handleClose.bind(this)}>
						<img src={close}/> 	
					</div>
				</div>
				{/*
				  * 	主体内容
				  * */}
				<div className='con' ref='con'>
					{/*
					  * 首页	
					  * */}
					<Home/>
					{/*
					  * 	子公司页
					  */}
					<ChildCompany/>
					{/*
					  * 	数据产品页
					  */}
					<DataProduct/>
					{/*
					  * 	营销服务页
					  */}
					<Marketing click={this.handleShow.bind(this,1)}/>
					{/*
					  * 	电商产品
					  */}
					<ECproduct/>
					{/*
					  * 	关于我们
					  */}
			        <About click={this.handleShow.bind(this)}/>
			        {/*
			          * 	联系我们
			          */}
			        <Contact/>
			    </div>
			    {/*
			      * 	蒙版遮罩
			      **/}
			    <div className='mask' ref='mask'></div>
			    {/*
			      * 详情页	
			      * */}
			    <Details ref='closePaging' 
				    closePaging={this.closePaging.bind(this)} 
				    val={this.state.val}
				    linkType={this.state.linkType} 
				    detailsType1_data={this.state.detailsType1_data} 
				    num={this.state.num} 
				    intro_data={this.state.intro_data} 
				    news_data={this.state.news_data} 
				    history_data={this.state.history_data} 
				    recruit_data={this.state.recruit_data}
			    />
			    {/*
			      * 	侧栏导航和尾部
			      **/}
			    <SideNav ref='sideNav' handleClick={this.tabConClick.bind(this)}/>
				<Footer ref='footer'/>
			</div>
		);
	}
}

export default App;


