import React,{Component} from 'react';
import '../css/home.css';
import dou from '../img/home_dou.png';
import dou1 from '../img/home_dou1.png';
import dou2 from '../img/home_dou2.png';
import dou3 from '../img/home_dou3.png';
import ct_center from '../img/ct_center.png';


class Home extends Component {
	constructor(){
		super()
	}
	componentDidMount(){
		function Random(min,max){
			return Math.random()*(max+1-min)+min;
		}
		/*
		 * 创建会转的小点
		 */
		for(var i=0;i<100;i++){
			this.refs.debris.innerHTML+='<div></div>'
			var a=Random(2,12);
			this.refs.debris.children[i].style.width=a+'px';
			this.refs.debris.children[i].style.height=a+'px';
			this.refs.debris.children[i].style.opacity=Random(1,10)/10;
			this.refs.debris.children[i].style.transform='translate3d('+((Math.round(Random(0,2))%2)?'-':'+')+Random(0,500)+'px,'+((Math.round(Random(0,2))%2)?'-':'+')+Random(0,500)+'px,'+((Math.round(Random(0,2))%2)?'-':'+')+Random(0,300)+'px'+')';
		}
	}
	/*
	 * 鼠标移入元素后的事件处理
	 */
	shakeIn(e){
		clearTimeout(this.tim);
		clearTimeout(this.tim1);
		this.refs.shake.children[0].style.animation= 'Flickering .4s linear forwards';
		this.refs.shake.children[1].style.animation= 'Flicker .4s linear forwards';
		this.tim=setTimeout(()=>{
			this.refs.shake.children[0].style= '';
			this.refs.shake.children[1].style= '';
			this.refs.shake.style.opacity=0;
			var dm=this.refs.circle_txt.children;
			for(var i=0;i<dm.length;i++){
				dm[i].children[0].style.animation='Flicker .4s linear forwards';
				dm[i].children[1].style.animation='Flickering .4s linear forwards';
				dm[i].children[2].style.animation='Flickering .4s linear forwards';
				if(dm[i].children.length<3){dm[i].children[3].style.animation='Flickering .4s linear forwards';}
				this.tim1=setTimeout(function(i){
					dm[i].children[0].style.animation='';
					dm[i].children[1].style.animation='';
					dm[i].children[2].style.animation='';
					if(dm[i].children.length<3){dm[i].children[3].style.animation='';}
					dm[i].children[0].style.opacity='1';
				}.bind(this,i),400)
			}
		},400)
	}
	/*
	 * 鼠标移出元素后的事件处理
	 */
	shakeOut(e){
		clearTimeout(this.tim);
		clearTimeout(this.tim1);
		var dm=this.refs.circle_txt.children;
		for(var i=0;i<dm.length;i++){
			dm[i].children[0].style.animation='Flicker .4s linear forwards';
			dm[i].children[1].style.animation='Flickering .4s linear forwards';
			dm[i].children[2].style.animation='Flickering .4s linear forwards';
			if(dm[i].children.length<3){dm[i].children[3].style.animation='Flickering .4s linear forwards';}
			this.tim1=setTimeout(function(i){
				dm[i].children[0].style.animation='';
				dm[i].children[1].style.animation='';
				dm[i].children[2].style.animation='';
				if(dm[i].children.length<3){dm[i].children[3].style.animation='';}
				dm[i].children[0].style.opacity='0';
				this.refs.shake.style.opacity=1;
				this.refs.shake.children[0].style.animation= 'Flickering .4s linear forwards';
				this.refs.shake.children[1].style.animation= 'Flicker .4s linear forwards';
				this.tim=setTimeout(()=>{
					this.refs.shake.children[0].style= '';
					this.refs.shake.children[1].style= '';
				},200)
			}.bind(this,i),400)
		}
	}
	render(){
		return (
			<div className='home default'>
				{/*
				  * 	旋转背景*/}
				<div className='circleRotate'>
					<div className='rotate1'></div>
					<div className='rotate2'></div>
					<div className='rotate3'></div>
					<div className='rotate4'></div>
				</div>
				{/*
				  * 	旋转小点*/}
				<div className='debris' ref='debris'></div>
				{/*鼠标移入会抖的元素*/}
				<div className='shake' ref='shake' >
					<img src={dou} className='shake_fir'/>
					<div className='shake_two'>
						<img src={dou1}/>
						<img src={dou2}/>
						<img src={dou3}/>
					</div>
				</div>
				{/*
				  * 	鼠标移入出来的元素*/}
				<div ref="center-mouse" className='center-mouse' onMouseEnter={this.shakeIn.bind(this)} onMouseLeave={this.shakeOut.bind(this)}>
					<div ref="circle_txt" className="circle-txt">
						<div className="ct_txt ct_txt1">
							<p>数字广告</p>
							<span></span><span></span>
						</div>
						<div className="ct_txt ct_txt2">
							<p>技术开发</p>	
							<span></span><span></span>
						</div>
						<div className="ct_txt ct_txt3">
							<p>数字营销</p>
							<span></span><span></span>
						</div>
						<div className="ct_txt ct_txt4">
							<p>电商渠道</p>
							<span></span><span></span>
						</div>
						<div className="ct_txt ct_txt5">
							<p>品牌营销</p>
							<span></span><span></span>
						</div>		
 						<div className="ct_txt ct_txt6">
                  	        <p>社会化广告</p>
                            <span></span><span></span>
                   		</div>                                                                                                             																
						<div className="ct_center">
							<img src={ct_center} className='ct_center_img' style={{width:'400px',height:'95px'}}/>
							<span></span><span></span><span></span>
						</div>
					</div>
				</div>
			</div>
		)
	}
}

export default Home;


