import React,{Component} from 'react';
import '../../css/detailTypes/news.css';

class News extends Component {
	constructor(){
		super();
		this.state={
			start:0,
			end:10
		}
	}
	/*
	 * 返回上一级  列表页
	 */
	handleBack(){
		this.refs.news_detail.style.opacity='0';
		this.refs.news_items.style.display='block';
		setTimeout(()=>{
			this.refs.news_detail.style.display='none';
		},300)
	}
	
	/*
	 * 进入详情页
	 */
	itemShow(num,type){
		/*
		 * num:点击的那一条
		 * type:Number类型   0代表获取当前id的详情 1下一个   -1  上一个
		 */
		this.refs.news_detail.style.display='block';
		setTimeout(()=>{
			this.refs.news_detail.style.opacity='1';
			this.refs.news_items.style.display='none';
		},300)
		this.num=num;
		this.getData(num,type)
	}
	/*
	 * 调取数据
	 */
	getData(num,type){
		switch(type){
			case 0:var Url='http://localhost:8004/news/getDetail';break;
			case -1:var Url='http://localhost:8004/news/getDetailPrev';break;
			case 1:var Url='http://localhost:8004/news/getDetailNext';break;
		}
		fetch(Url,{
			method:'post',
			headers:{
		    	"Content-type":"application/x-www-form-urlencoded; charset=UTF-8"
		    },
			body:`id=${num}`
		}).then(res=>{
			if(res.ok){
				return res.json()
			}
		}).then(res=>{
			if(!res.length) return;
			if(type== 1 || type == -1) this.num=res[0].id;
			this.refs.content.innerHTML=res[0].content;
			this.setState({})
		}).catch(e=>{
			console.log(e)
		})
	}
	/*
	 * 上一页
	 */
	prev(boo){
		boo?
		this.state.start>0 && this.setState({
			start:this.state.start - 10,
			end:this.state.end - 10
		}):this.getData(this.num,-1)
	}
	/*
	 * 下一页
	 */
	next(boo){
		boo?
		this.state.end<this.props.data.length && this.setState({
			start:this.state.start + 10,
			end:this.state.end + 10
		}):this.getData(this.num,1)
	}
	render(){
		return (
			<div className='news'>
				<div className='news_items' ref='news_items'>
					<div className='controller'>
						{/*
						  * 	上一个
						  */}
						<a href='javascript:;' className='ctl_prev' onClick={this.prev.bind(this,true)}>
							<span></span>
							<span></span>
						</a>
						{/*
						  * 	下一个
						  */}
						<a href='javascript:;' className='ctl_next' onClick={this.next.bind(this,true)}>
							<span></span>
							<span></span>
						</a>
					</div>
					{/*
							  * 	列表项
							  */}
					{
						this.props.data.slice(this.state.start,this.state.end).map((v,k)=>(
							
							<div className='news_item' key={k} onClick={this.itemShow.bind(this,v.id,0)}>
								<div className='img_box'>
									<img src={v.img}/>
								</div>
								<div className='txt_box'>
									<h3>{v.tit}</h3>
									<p>
									作者：<span>{v.writer}</span><span>{v.time}</span>
									</p>
								</div>
							</div>
						))
					}
				</div>
				<div className='news_detail' ref='news_detail'>
					<div className='controller'>
						{/*
						  * 	回到上一级
						  */}
						<a href='javascript:;' className='ctl_back' onClick={this.handleBack.bind(this)}>{(function(){
								var arr=[];
								for(var i=0;i<9;i++){
									arr.push(<span key={i}></span>)
								}
								return arr;
							}())}
						</a>
						{/*
						  * 	上一个
						  */}
						<a href='javascript:;' className='ctl_prev' onClick={this.prev.bind(this,false)}>
							<span></span>
							<span></span>
						</a>
						{/*
						  * 	下一个
						  */}
						<a href='javascript:;' className='ctl_next' onClick={this.next.bind(this,false)}>
							<span></span>
							<span></span>
						</a>
					</div>
					<div ref='content'></div>	
				</div>
			</div>
		)
	}
}
export default News;


